import React from 'react';
import Card from './card.jsx'

var products =[{
    name:"Chair",
    src:"https://images-na.ssl-images-amazon.com/images/I/41Jgo7WjH1L.jpg",
    description:"Leap into electronic typesitting,remaining essentially unchanged. SO BUY NOW!"
},
{
    name:"Table",
    src:"https://images-na.ssl-images-amazon.com/images/I/61lfXal0EnL._SX425_.jpg",
    description:"Leap into electronic typesitting,remaining essentially unchanged. SO BUY NOW!"
},
{
    name:"sofa",
    src:"https://images-na.ssl-images-amazon.com/images/I/61cUDBHk-tL._SX425_.jpg",
    description:"Leap into electronic typesitting,remaining essentially unchanged. SO BUY NOW!"
}

]
function product(){
    return(
        <div>
            {products.map(function(i) {return(<Card name={i.name} description ={i.description} src={i.src} />)})}
        </div>
    );
}
export default product;